<template>
  <div>
    <h2>Login</h2>
    <div class="row">
      <div class="card mx-auto">
        <div class="card-header text-white bg-primary pl-1rem">
          <h4>Login</h4>
        </div>
      </div>
      <div class="card mx-auto">
        <form @submit.prevent="loginUser">
          <div class="form-group">
            <label for="username" class="pl-1rem">Username</label>
            <input
              id="username"
              type="text"
              placeholder="Username"
              name="username"
              v-model="username"
              class="form-control pl-2rem"
            />
          </div>
          <div class="form-group ">
            <label for="password" class="pl-1rem">Password</label>
            <input
              id="password"
              type="password"
              placeholder="Password"
              name="password"
              v-model="password"
              class="form-control pl-2rem"
            />
          </div>
          <input type="submit" class="btn btn-primary ma-1rem" value="login" />
          &nbsp;&nbsp;&nbsp;&nbsp;
          <router-link to="/register" class="card-link">
            Need an account?
          </router-link>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  data() {
    return {
      username: "",
      password: "",
    };
  },
  methods: {
    ...mapActions(["login"]),
    loginUser() {
      let user = {
        username: this.username,
        password: this.password,
      };
      this.login(user)
        .then((res) => {
          if (res.data.success) {
            this.$router.push("/profile");
          }
        })
        .catch((err) => console.log(err));
    },
  },
};
</script>

<style scoped></style>
