<template>
  <div>
    <h2>Register</h2>
    <div class="row">
      <div class="card mx-auto">
        <div class="card-header text-white bg-primary pl-1rem">
          <h4>Register</h4>
        </div>
      </div>
      <div class="card mx-auto">
        <form @submit.prevent="registerUser">
          <div class="form-group">
            <label for="username" class="pl-1rem">Username</label>
            <input
              id="username"
              type="text"
              placeholder="Username"
              name="username"
              v-model="username"
              class="form-control pl-2rem"
            />
          </div>
          <div class="form-group">
            <label for="name" class="pl-1rem">Name</label>
            <input
              id="name"
              type="text"
              placeholder="Name"
              name="name"
              v-model="name"
              class="form-control pl-2rem"
            />
          </div>
          <div class="form-group">
            <label for="Email" class="pl-1rem">Email</label>
            <input
              id="email"
              type="email"
              placeholder="Email"
              name="email"
              v-model="email"
              class="form-control pl-2rem"
            />
          </div>
          <div class="form-group ">
            <label for="password" class="pl-1rem">Password</label>
            <input
              id="password"
              type="password"
              placeholder="Password"
              name="password"
              v-model="password"
              class="form-control pl-2rem"
            />
          </div>
          <div class="form-group ">
            <label for="confirm_password" class="pl-1rem"
              >Confim_Password</label
            >
            <input
              id="confirm_password"
              type="password"
              placeholder="Confirm_Password"
              name="confirm_password"
              v-model="confirm_password"
              class="form-control pl-2rem"
            />
          </div>
          <button class="btn btn-primary ma-1rem">Register</button>
          &nbsp;&nbsp;&nbsp;&nbsp;
          <router-link to="/login" class="card-link">
            Already have an account?
          </router-link>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  data() {
    return {
      username: "",
      name: "",
      email: "",
      password: "",
      confirm_password: "",
    };
  },
  methods: {
    ...mapActions(["register"]),
    registerUser() {
      let user = {
        username: this.username,
        password: this.password,
        confirm_password: this.confirm_password,
        email: this.email,
        name: this.name,
      };
      this.register(user).then((res) => {
        if (res.data.success) {
          this.$router.push("/login");
        }
      });
    },
  },
};
</script>

<style lang="scss" scoped></style>
